












































.Anime-container {
  margin-bottom: 20px;

  .buttons-container {
    display: flex;
    justify-content: center;
    padding: 10px 0;

    .btn {
      margin-left: 10px;

      &:first-child {
        margin-left: 0;
      }
    }

    @media (max-width: 600px) {
      flex-direction: column;
      margin: 0 10px 10px 10px;

      .btn {
        margin-left: 0 !important;
        margin-bottom: 15px;
      }
    }
  }
}
